.maincontainer-contact {
  max-width: 1240px;
  margin: auto;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#contact {
  background: var(--green1-color);
  padding: 3%;
  min-height: 50vh;
}
.container-contact-left {
  min-width: 400px;
  margin: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 10px;
  flex-wrap: wrap;
}
.container-contact-right {
  min-width: 400px;
  margin: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.container-socialmedia {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-self: center;
  gap: 20px;
  padding: 20px;
  margin-top: 30px;
}
.header-text-contact-form {
  font-size: 30px;
  font-weight: bold;
  /* margin-bottom: 20px; */
  color: var(--green3-color);
  align-self: center;
}
.header-text-contact-form-disable {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 20px;
  margin-top: -20px;
  color: var(--green3-color);
  align-self: center;
}

.contact-component-text {
  margin-right: 20px;
  font-size: 20px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
}
.icon-contact {
  width: 30px;
  height: auto;
  margin-right: 20px;
}
.message-textfield {
  background-color: var(--background-color);
  width: 100%;
}
.button-component {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 50px;
}
.button-contact-style-send {
  min-width: 130px;
  min-height: 40px;
  background-color: var(--green2-color);
  padding: 10px;
  border-radius: 15px;
  font-weight: bold;
  color: var(--background-color);
  border-color: var(--green3-color);
  border-style: solid;
}
.button-contact-style-cancel {
  min-width: 130px;
  min-height: 40px;
  background-color: var(--orange1-color);
  padding: 10px;
  border-radius: 15px;
  border-color: var(--green3-color);
  font-weight: bold;
  color: var(--background-color);
  border-style: solid;
}
.button-contact-style-send:hover {
  background-color: var(--green3-color);
  transition-duration: 0.4s;
}
.button-contact-style-cancel:hover {
  transition-duration: 0.4s;
  background-color: var(--orange2-color);
}
.button-component-download {
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 20px;
  border: 2px solid var(--green3-color);
  border-radius: 15px;
  padding: 10px;
}
.button-download-style-cv {
  min-width: 130px;
  min-height: 40px;
  background-color: var(--green2-color);
  padding: 10px;
  border-radius: 15px;
  font-weight: bold;
  color: var(--background-color);
  border-color: var(--green3-color);
  border-style: solid;
}
.button-download-style-resume {
  min-width: 130px;
  min-height: 40px;
  background-color: var(--green2-color);
  padding: 10px;
  border-radius: 15px;
  border-color: var(--green3-color);
  font-weight: bold;
  color: var(--background-color);
  border-style: solid;
}
.button-download-style-cv:hover {
  background-color: var(--background2-color);
  color: var(--green3-color);
  transition-duration: 0.4s;
}
.button-download-style-resume:hover {
  transition-duration: 0.4s;
  color: var(--green3-color);
  background-color: var(--background2-color);
}

@media screen and (max-width: 940px) {
  .maincontainer-contact {
    max-width: 1240px;
    margin: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 50px;
  }
  .container-contact-right {
    order: 0;
  }
  .container-contact-left {
    order: 1;
  }
  .header-text-contact-form {
    margin-top: 20px;
  }
}
@media screen and (max-width: 550px) {
  .container-contact-right {
    min-width: 320px;
  }
  .container-contact-left {
    min-width: 320px;
  }
  .button-component-download {
    width: 300px;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 20px;
    border: 2px solid var(--green3-color);
    border-radius: 15px;
    padding: 10px;
  }
}
